  methods:{
    resume:async function(){
        await this.renderSlowPage()
        await this.renderSlowStyle()
    },
    renderSlowPage:function(){
      return new Promise((resolve,reject)=>{
        let interval = this.interval
        let length = this.fullPage.length
        let rendPage = ()=>{
          if (this.currentPage.length<length) {
            let chart = this.fullPage.substring(0,this.currentPage.length+1)

            this.currentPage = chart
            setTimeout(rendPage,interval)
          } else {
            resolve()
          }
        }
        rendPage()
      })
    },
    renderSlowStyle:function(){
      return new Promise((resolve,reject)=>{
        let interval = this.interval
        let length = this.fullStyle.length
        let rendStyle = ()=>{
          if (this.currentStyle.length<length) {
            let chart = this.fullStyle.substring(0,this.currentStyle.length+1)
            this.currentStyle = chart
            setTimeout(rendStyle,5)
          } else {
            resolve()
          }
        }
        rendStyle()
      })
    }
